* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.App {
  max-width: 1920px;
  margin: 0 auto;
  height: 100vh;
}

.header {
  position: absolute;
  z-index: 30;
  top: 4%;
  left: 10%;
  cursor: pointer;
}

.footer-logo {
  position: absolute;
  z-index: 30;
  bottom: 4%;
  left: 10%;
  cursor: pointer;
}
.popup {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(22, 39, 62, 0.5);
  &-content {
    margin: 0 auto;
    padding-left: 21%;
    padding-top: 10%;
    padding-right: 6%;
    text-align: left;
    &__title {
      text-transform: uppercase;

      & h1 {
        font-size: 7em;
        font-weight: 400;
      }

      & h2 {
        font-size: 2.2em;
        font-weight: 500;
      }
    }
    &__text {
      height: 400px;
      & span {
        color: #8cc9e8;
        font-size: 30px;
        font-weight: 700;
        display: inline-block;
        margin: 15px 0;
      }
      & div {
        font-size: 30px;
      }

      & div.track_container-dots {
        bottom: auto;
      }
    }
  }
  &-slider {
    background-color: white;
    position: absolute;
    height: 68%;
    width: 67%;
    top: 21%;
    left: 18%;
    border-radius: 7%;
  }
  &-btn {
    position: absolute;
    top: 23%;
    left: 76%;
    z-index: 30;
  }

  &-slider {
    text-align: center;
  }
}

.track {
  width: 100%;
  height: 100%;
  position: relative;

  &_right-arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%) rotate(-180deg);
    right: 2%;
    z-index: 10;
    border: none;
    outline: none;
    background-color: transparent;
  }
  &_left-arrow {
    position: absolute;
    transform: translateY(-50%);
    top: 50%;
    left: 2%;
    z-index: 10;
    border: none;
    outline: none;
    background-color: transparent;
  }
  &_container-dots {
    position: absolute;
    bottom: 5%;
    width: 100%;
    height: 50px;
    display: flex;
    justify-content: center;
    padding-top: 20px;
  }
  &_dot {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 2px solid #000;
    margin: 0 5px;
    z-index: 10;
  }
  &_dot.active {
    background-color: #f86fa2;
    border: none;
  }
}

.slider {
  display: flex;
  width: 100%;
  height: 100%;
  overflow-x: hidden;

  & > div {
    min-width: 100%;
    transition: 0.3s linear;
  }
}

.slide1 {
  background-image: url('./img/slider1-bg.jpg');
  background-size: 100% 100%;
  position: relative;
  &-btn {
    padding-left: 85%;
    margin: -120px;
  }
  &-text {
    padding: 0px 80px;
    margin-top: 190px;
  }
  &-title {
    font-size: 40px;
    font-family: 'Bitter', serif;
  }
  &-subtitle {
    font-size: 160px;
    font-family: 'Bitter', serif;
    font-weight: 300;
    line-height: 1em;
    margin-top: -20px;
    z-index: 10;
    & span {
      font-weight: 400;
    }
  }
  &-bacterium {
    height: 150px;
    position: absolute;
    top: 10%;
    right: 5%;
    animation: Orbita 20s linear infinite alternate;
    z-index: 5;
    & img {
      height: 100px;
    }
  }
  &-bacterium2 {
    position: absolute;
    right: 8%;
    top: 5%;
    z-index: 4;
    animation: forward 20s linear infinite alternate;
    & img {
      height: 70px;
    }
  }
  &-bacterium3 {
    position: absolute;
    left: 8%;
    bottom: 30%;
    z-index: 4;
    animation: diagonal 15s linear infinite alternate;
    & img {
      height: 70px;
    }
  }
  // background-size: cover;
}

.big {
  position: relative;
  margin-top: 40px;
}
.slide2 {
  background-image: url('./img/slide2-bg.jpg');
  background-size: 100% 100%;
  padding: 0px 80px;
  font-family: 'Bitter', serif;
  font-weight: 300;

  &-title {
    width: 370px;
    font-size: 45px;
    margin-top: 160px;
  }
  &-container {
    overflow-y: scroll;
    height: 500px;
  }

  &-window {
    position: absolute;

    box-shadow: 4px 3px 25px 0px rgba(53, 113, 159, 0.2);
    border-radius: 30px;
    height: 104%;
    width: 81.5%;
    top: -1%;
    left: -1%;
    border: 25px solid #ffffff;
  }
  &-text {
    width: 80%;
    font-size: 24px;
    height: 600px;
    font-weight: 400;
    line-height: 1.5em;
    background-color: #ffffff;
    padding: 50px;
  }
}

.slide3 {
  background-image: url('./img/slide3-bg.jpg');
  background-size: 100% 100%;
  text-align: left;
  font-family: 'Bitter', serif;
  font-weight: 300;

  &-sprey {
    &_img {
      position: absolute;
      z-index: 10;
      height: 84%;
      left: 5%;
      bottom: 0%;
    }

    &_buble1 {
      position: absolute;
      z-index: 15;
      height: 10em;
      opacity: 0.7;
      left: 25%;
      bottom: 6%;
      animation: diagonal-reverse 15s linear infinite alternate;
    }
    &_buble5 {
      position: absolute;
      height: 3em;
      left: 15%;
      top: 12%;
      animation: diagonal 10s linear infinite alternate-reverse;
    }
  }

  &-title h1 {
    text-transform: uppercase;
    margin: 0 auto;
    width: 600px;
    font-weight: 400;
    line-height: 6rem;
    margin-top: 230px;
    padding-left: 12rem;
    & span {
      font-size: 6rem;
      letter-spacing: 10px;
      & span {
        font-weight: 700;
      }
    }
  }
}
.container {
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  flex-wrap: wrap;
}
.container2 {
  display: flex;
  flex-direction: column;
  margin-left: 30px;
  & div.custom-btn {
    margin-top: 40px;
  }
}
.info-table {
  width: 340px;
  font-size: 25px;
  background-color: #ffffff;
  box-shadow: 4px 3px 61px -22px rgba(53, 113, 159, 20%);
  padding: 50px;
  border-radius: 30px;
}
.custom-btn {
  width: 320px;
  height: 100px;
  background-color: #ff6a9f;
  border-radius: 80px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;

  &_arrow {
    width: 50px;
    height: 50px;
    background-color: #171717;
    border-radius: 50%;
    position: relative;
    color: #ffffff;
    & span {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 34px;
    }
  }
  &_text {
    font-size: 30px;
    font-weight: 400;
  }
}

.slide2-container::-webkit-scrollbar {
  margin-left: 30px;
  width: 10px;
  height: 29px;
}

/* Track */
.slide2-container::-webkit-scrollbar-track {
  background: #000000;
  border: 4px solid transparent;
  background-clip: padding-box;
}

/* Handle */
.slide2-container::-webkit-scrollbar-thumb {
  background: #cf578d;
  border-radius: 8px;
  border: 0px solid transparent;
}

@keyframes forward {
  from {
    transform: translateX(0px);
  }
  to {
    transform: translateX(-160px);
  }
}
@keyframes diagonal {
  from {
    transform: translateX(0px);
  }
  to {
    transform: translateX(100px) translateY(80px);
  }
}
@keyframes Orbita {
  from {
    transform: translateX(0px) rotate(0deg);
  }
  to {
    transform: translateX(-100px) rotate(-360deg);
  }
}

@keyframes size {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.3);
  }
}

@keyframes diagonal-reverse {
  from {
    transform: translateX(0px);
  }
  to {
    transform: translateX(-90px) translateY(-80px);
  }
}

@import './media';
